.header {
  &__burger-button {
    background-color: transparent;
    display: inline-flex;
    align-items: center;

    & svg *[fill] {
      fill: revert-layer;
    }

    &:hover {
      opacity: 0.5;
    }
  }

  &__inner {
    display: flex;
    flex-direction: row;
    height: 90px;
    justify-content: space-between;
  }

  &__list-burger-wrapper {
    display: flex;
    column-gap: 30px;
  }

  &__logo {
  }

  &__menu {
    --headerMenuPaddingInline: 16px;

    display: inline-flex;
    align-items: center;

    &-list {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 48px;

      

      &--short-dist {
        --headerMenuPaddingInline: 10px;
      }
    }

    &-link {
      padding-inline: var(--headerMenuPaddingInline);
      display: inline-flex;
      align-items: center;
      height: 100%;
    }

    &-item {
      font-weight: bold;
      height: 100%;
    }
  }
}
